  display: list-item;
}

body {
  font-family: "Open Sans", Helvetica, sans-serif;
  font-weight: 400;
  background: #333;
  color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  color: #fff;
  display: inline-block;
}

a:hover, a:focus {
  text-decoration: none;
}

button,
[type='button'] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: 1px solid #fff;
  border-radius: 3px;
  padding: 5px 10px;
  color: #fff;
  background: none;
  -webkit-box-shadow: none;
          box-shadow: none;
}

button:hover, button:focus,
[type='button']:hover,
[type='button']:focus {
  background: #fff;
  color: #333;
}

.container {
  max-width: 994px;
  margin: 0 auto;
}

@media (max-width: 1023px) {
  .container {
    padding: 0 15px;
  }
}

.profiles__list {
  border-radius: 6px;
}

.profiles-list__item {
  border: 1px solid #fff;
  border-bottom-width: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

@media (max-width: 319px) {
  .profiles-list__item {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    padding: 10px;
  }
}

@media (min-width: 320px) {
  .profiles-list__item {
    padding: 20px;
  }
}

.profiles-list__item:last-of-type {
  border-bottom-width: 1px;
}

@media (max-width: 319px) {
  .profiles-list__link {
    margin-bottom: 15px;
  }
}
